.table-header-div {
	display: inline;
}
.table-header-div div {
	margin-top: 5px;
	margin-bottom: 5px;
	display: block;
}

.table-header-div input {
	width: 50px;
}

th {
	padding: 10px;
	background-color: rgb(0, 160, 160);
	color: white;
	border: 1px solid black;
}

td {
	border: 1px solid rgb(0, 160, 160);
}

label {
	font-size: 0.8rem;
}

hr {
	border: 1px solid white;
}
